  font-family: 'Heebo', sans-serif;
  height: 100%;
  text-align: center;
  width: 100%; }

.pingbot {
  height: calc(100% - 30px);
  width: calc(100% - 30px);
  border: 15px solid #111; }

.pingbot_scores {
  display: -ms-flexbox;
  display: flex;
  height: 100%; }
  @media (orientation: landscape) {
    .pingbot_scores {
      -ms-flex-direction: row;
          flex-direction: row; } }
  @media (orientation: portrait) {
    .pingbot_scores {
      -ms-flex-direction: column;
          flex-direction: column; } }

.pingbot_blueScore,
.pingbot_redScore {
  -ms-flex-align: center;
      align-items: center;
  background: #222;
  border: none;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  font-size: 400px;
  font-weight: bold;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  max-width: 50%;
  outline: none;
  overflow: hidden;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 100%;
  -webkit-transition: background 0.5s ease-in-out;
  -o-transition: background 0.5s ease-in-out;
  transition: background 0.5s ease-in-out; }
  .pingbot_blueScore:active,
  .pingbot_redScore:active {
