.pingbot_blueScore {
  font-family: 'Heebo', sans-serif;
  color: #03afff; }

.pingbot_redScore {
  font-family: 'Heebo', sans-serif;
  color: #ff2a04; }

.pingbot_resetScore {
  background: #111;
  border-radius: 3px;
  bottom: 0px;
  color: #FFF;
  cursor: pointer;
  display: inline-block;
  font-weight: 100;
  left: 50%;
