      right: 0;
      width: 150px; } }

.pingbot_servingRed,
.pingbot_servingBlue {
  background: #111;
  bottom: 0px;
  color: #FFF;
  cursor: pointer;
  padding: 7.5px 15px;
  position: absolute;
  text-transform: uppercase; }

.pingbot_servingRed {
  left: 0; }
  @media (orientation: portrait) {
    .pingbot_servingRed {
      bottom: auto;
      top: 0; } }

.pingbot_servingBlue {
  right: 0; }
  @media (orientation: portrait) {
    .pingbot_servingBlue {
      right: auto;
      left: 0;
      bottom: 0; } }



